html {
  height: 100%;
  background: linear-gradient(to top, #9CECFB, #65C7F7, #0052D4) no-repeat center center fixed;
  background-size: cover;
}
body {
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Roseritta Regular';
  font-style: normal;
  font-weight: normal;
  src: local('Roseritta Regular'), url(/majinblue/robofriends/static/media/Roseritta.020a72f0.woff) format('woff');
}

h1 {
  font-family: 'Roseritta Regular';
  color: white;
  letter-spacing: .5rem;
}
